@import 'core/variables';
@import 'core/reset';
* {
  font-family: 'Open Sans', sans-serif;
}
.header {
  background-color: $lightgrey;
  height: 10vh;
  -webkit-box-shadow: 0px 10px 13px -7px #00000071,
    5px 5px 15px 5px rgba(245, 245, 245, 0);
  box-shadow: 0px 10px 13px -7px #00000071,
    5px 5px 15px 5px rgba(245, 245, 245, 0);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.header__form {
  font-family: 'Open Sans', sans-serif;
}
